body {
    font-family: Arial, sans-serif;
    text-align: center;
    background-color: #f4f4f4;
    margin: 0;
    padding: 0;
}

.container {
    max-width: 600px;
    margin: 50px auto;
    background: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

h1 {
    color: #333;
}

p {
    color: #555;
}

.skills {
    margin-top: 20px;
}

.skill {
    background: #007bff;
    color: white;
    display: inline-block;
    padding: 8px 15px;
    margin: 5px;
    border-radius: 5px;
}
